@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.section {
    height: 100vh;
    background-color: #282c34;
    color: #fff;
    transition: .5s all linear;
}

.light_mode.section {
    background-color: #fff;
}

.container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-evenly;
}

.switch_btn {
    position: absolute;
    top: 0px;
    right: 15px;
    padding-top: 10px;

}
.title {
    font-size: 6vw;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 35vh;
    width: 100%;
}

.inputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.search,
.select {
    width: 43%;
}

@media (max-width: 900px) {
    .header {
        height: 25vh;
    }
    .title {
        font-size: 11vw;
    }
}
.meaning {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 55vh;
    border: 10px solid rgba(105, 150, 105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.sub_title {
    font-size: 4vw;
    font-family: 'Montserrat', sans-serif;
}

.single_mean {
    background-color: #fff;
    color: #000;
}

.single_mean hr {
    background-color: #000;
    width: 100%;
}

.single_mean {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}

.single_mean audio {
    background-color: #fff;
    border-radius: 10px;
}

@media (max-width: 900px) {
    .meaning {
        height: 60vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}


/* light mode */
.light_mode .single_mean {
    background-color: #3b5360;
    color: #fff;
}

.light_mode .single_mean hr {
    background-color: #000;
    width: 100%;
}
